.header{
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 90px;
  background-color:  #120826;
  padding-left: 50px;
  padding-right: 50px;
  color: #fff;
  &__burger{
    cursor: pointer;
    display: none;
  }
  &__nav{
    display: flex;
    justify-content: center;
  }
}

.nav{
  &__link{
    color: #fff;
    font-size: 16px;
    line-height: 24px;
    margin-left: 8px;
    margin-right: 8px;
    text-transform: uppercase;
  }
}